import * as React from 'react';
import { Controller, Plugin } from '@ali/4ever-cangjie';
import{ InlinePlaceholder, Placeholder } from '@ali/4ever-component';
import { removePlaceholder } from './actions';
import { PlaceHolderPluginConfig } from './types';

export default function createRenderNode(config: PlaceHolderPluginConfig): Plugin['renderNode'] {
  const locale = config.locale || {};
  const { renderText } = config;
  return {
    ph: (props, controller) => {
      const { node } = props;
      return (
        <Placeholder
          {...props}
          handleRemoveNode={() => controller.run('onAction', removePlaceholder(node))}
          editor={controller}
          renderText={renderText}
          locale={locale}
        />
      );
    },
    iph: (props, controller) => {
      return (
        <InlinePlaceholder
          {...props}
          editor={controller}
          renderText={renderText}
        />
      );
    },
  };
}
